'use client';

import React from 'react';
import { Button } from '@/components/ui/button';
import { LayoutGrid, Calculator as CalcIcon, GraduationCap } from 'lucide-react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

export default function Header() {
    const pathname = usePathname();

    return (
        <header className="fixed top-0 left-0 right-0 z-50 transition-all duration-300">
            <div className="backdrop-blur-xl bg-white/70 border-b border-slate-100 px-6 py-4 max-w-7xl mx-auto flex items-center justify-between shadow-sm">
                <div className="flex items-center gap-3">
                    <div className="w-10 h-10 bg-emerald-600 rounded-2xl flex items-center justify-center shadow-lg shadow-emerald-200">
                        <GraduationCap className="text-white w-6 h-6" />
                    </div>
                    <div>
                        <h2 className="text-lg font-black text-slate-900 tracking-tight leading-none">Academia Planner</h2>
                        <p className="text-[10px] font-bold text-slate-400 uppercase tracking-widest mt-1">IIT Madras BS Official</p>
                    </div>
                </div>

                <nav className="flex items-center gap-2 p-1.5 bg-slate-100 rounded-2xl">
                    <Link href="/">
                        <Button
                            variant={pathname === '/' ? 'default' : 'ghost'}
                            size="sm"
                            className={`rounded-xl px-4 font-black text-xs transition-all ${pathname === '/' ? 'bg-white text-emerald-600 shadow-sm border border-slate-100 hover:bg-white' : 'text-slate-500 hover:bg-slate-200'}`}
                        >
                            <LayoutGrid className="w-3.5 h-3.5 mr-2" /> Current Status
                        </Button>
                    </Link>
                    <Link href="/predict">
                        <Button
                            variant={pathname === '/predict' ? 'default' : 'ghost'}
                            size="sm"
                            className={`rounded-xl px-4 font-black text-xs transition-all ${pathname === '/predict' ? 'bg-white text-emerald-600 shadow-sm border border-slate-100 hover:bg-white' : 'text-slate-500 hover:bg-slate-200'}`}
                        >
                            <CalcIcon className="w-3.5 h-3.5 mr-2" /> Predictive Model
                        </Button>
                    </Link>
                </nav>
            </div>
        </header>
    );
}
